const initialState = {
  settings_loaded : false,
  loading : false,
  description : {
    work_types : { name : " ", description : "  "},
    user_roles : { name : "  ", description : "      "},
    status_types : { name : " ", description : "     "}
  },
  data : {
    work_types : [

    ],
    user_roles : [

    ],
    status_types : [

    ]
  },
  newData : {},
  settingsUpdating : false
}

export function settingsReducer(state = initialState, action) {
  switch(action.type){
    case "CHANGE_VALUE" : 
      let newData = state.data;
      newData[action.itemType][action.itemPos][action.itemField] = action.value;
      return {
        ...state,
        newData
      }
    case "CREATE_VALUE" : 
      let newRow = {};
     // const key = "table";
     // newRow[key] = action.itemType;
      newRow[action.itemField] = action.value;
      return {
        ...state,
        newData : newRow
      }
    case "CLEAR_ROW" : 
      return {
        ...state,
        newData : {}
      }
    case "START_UPDATE" : 
      return {
        ...state,
        settingsUpdating : true
      }
    case "FINISH_UPDATE" : 
      return {
        ...state,
        settingsUpdating : false
      }
    case "START_LOADING" : 
      return {
        ...state,
        loading : true
      }
    case "SETTINGS_LOADED" : 
      const data = action.data;
      return {
        ...state,
        loading : false,
        settings_loaded : true,
        data
      }
    default:
      return state;
  }
}