import React from 'react'
import { withRouter, Link } from 'react-router-dom'
import { Breadcrumb, Icon } from 'antd';

const menuMap = {
    "/users" : "",
    "/users/edit" : " ",
    "/users/create" : "  ",
    "/customers" : "",
    "/customers/edit" : " ",
    "/customers/createcustomer" : " ",
    "/contracts" : "",
    "/contracts/edit" : " ",
    "/contracts/create" : " ",
    "/settings" : "",
    "/settings/work_types" : " ",
    "/settings/user_roles" : " ",
    "/settings/status_types" : " ",
}


/**
 *   
 * @param props - location props from react-router
 */
const TopCrumbs = withRouter((props) => {
    const { location } = props;
    const pathSnippets = location.pathname.split('/').filter(i => i);
    const extraBreadcrumbItems = pathSnippets.map((_, index) => {
      let url = '';
      if(index < 2){
        url = `/${pathSnippets.slice(0, index + 1).join('/')}`;
        return (
          <Breadcrumb.Item key={url}>
            <Link to={url}>
              {menuMap[url]}
            </Link>
          </Breadcrumb.Item>
        );
      }
    });
    const breadcrumbItems = [(
      <Breadcrumb.Item key="/">
        <Link to="/"></Link>
      </Breadcrumb.Item>
    )].concat(extraBreadcrumbItems);
    return(
        <Breadcrumb separator={<Icon type="right" theme="outlined" />}>
            {breadcrumbItems}
        </Breadcrumb>
    )
})

export default TopCrumbs;
