import React, { Component } from 'react'
import { Form, Input, Button, Checkbox, Select, Row, Col, Collapse } from 'antd'
import { Field, reduxForm } from 'redux-form'
import { Redirect } from 'react-router-dom'
import { connect } from 'react-redux'
import { getUser, updatedUser, insertUser} from '../../actions/UserListActions'
import { getSettings } from '../../actions/SettingsActions'
import Preloader from '../Preloader'
const FormItem = Form.Item;

const defaultGrid = {
    xs : 12,
    sm : 24, 
    md : 20,
    lg : 12,  
    xl : 8,
}

/**
 *  Redux field with antd https://codesandbox.io/s/jzyl70wpk
 */

const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 6 }
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 14 }
    }
  };

const makeField = Component => ({ input, meta, children, hasFeedback, label, ...rest }) => {
    const hasError = meta.touched && meta.invalid;
    return (
      <FormItem
        {...formItemLayout}
        label={label}
        validateStatus={hasError ? "error" : "success"}
        hasFeedback={hasFeedback && hasError}
        help={hasError && meta.error}
      >
        <Component {...input} {...rest} children={children} />
      </FormItem>
    );
  };

  const AInput = makeField(Input);
  const ACheckbox = makeField(Checkbox);
  const ASelect = makeField(Select);

/**
 *   
 *       
 * 1 - 
 * 2 - 
 * 3 - 
 */
class UserEdit extends Component{
    constructor(props){
      super(props);
      this.state={
        needToRedirect : false
      }
    }

    componentWillMount(){
      const { match, user, getOne, settings, loadSettings } = this.props;

      if(match && typeof match.params.id !== 'undefined'){
        getOne(user.id, match.params.id);
      }

      if(settings.settings_loaded){
        loadSettings()
      }
    }

    handleSubmit = values =>{
      const { user, updateOne, createOne } = this.props;
      delete values.re_password;
      delete values.status_text;
      delete values.is_active;
      delete values.role_data;

      if(values.is_active === true){
        values.is_active = 1;
      }
      console.log(values);
      if(values.id > 0){
        updateOne(user.id, values);
      }else{
        createOne(user.id, values);
      }
      this.setState({
            needToRedirect : true
      })
    }

    render(){
      const { user, handleSubmit, pristine, submitting, userFetch, settings, grid, initialValues } = this.props;
      if(this.state.needToRedirect) return(<Redirect to="/users"/>)
      if(userFetch) return <Preloader/>

      return(
        <Row type="flex" justify="left">
          <Col {...grid} >
            <Form onSubmit={handleSubmit(this.handleSubmit)}>
              <Field type="hidden" component="input" name="id"/>
              {user.id !== initialValues.id &&
                <Field label="" name="is_active" component={ACheckbox} type="checkbox" />
              }
              <Field label="" name="name" component={AInput} placeholder="" hasFeedback />
              <Field label="" name="surename" component={AInput} placeholder="" />
              <Field label="" name="login" component={AInput} placeholder="" />
              {this.props.userStatus === 1 &&
                <Field 
                  label="" 
                  component={ASelect}  
                  name="role_id"
                >
                  {settings.data.user_roles && settings.data.user_roles.map(role =>
                      <Select.Option value={role.id} key={role.id}>
                          {role.role_name}
                      </Select.Option>
                  )}
                </Field>
              }
              {this.props.userStatus === 1 &&
              (<Collapse bordered={false}>
                  <Collapse.Panel
                  style={{border:0}}
                  header=' '
                  >
                    <Field label="" name="password" component={AInput} type="password"  />
                    <Field label=" " name="re_password" component={AInput} type="password"  />
                  </Collapse.Panel>
              </Collapse>)
              }
              <Button htmlType="submit" type="primary" disabled={pristine || submitting }></Button>
            </Form>
          </Col>
        </Row>
      )
    }
}


const validate = values => {
  const errors = {};
  if(values.password !== values.re_password){
      errors.re_password="  .";
  }
  if(!values.name){
      errors.name = "   ."
  }
  if(/^[--]+$/.test(values.name) === false){
      errors.name = "        "
  }
  if(!values.login){
      errors.login = "  "
  }
  if(/^[a-zA-Z1-9]+$/.test(values.login) === false){
      errors.login = "        "
  }
  
  if(!values.role){
      errors.role = "   ";
  }
  
  return errors;
  }

function mapStateToProps(state, ownProps) {
  return {
    user : state.user,
    userStatus: state.user.role,
    role : ownProps.userRoles,
    settings : state.settings,
    userFetch : state.userList.userIsLoading,
    initialValues : state.userList.currentUserData,
    grid : ownProps.gridSettings  || defaultGrid
    //initialValues: {
    //    id: ownProps.initialValues.id ? ownProps.initialValues.id : 0, 
    //    is_active: ownProps.initialValues.is_active,
    //    name : ownProps.initialValues.name,
    //    surename : ownProps.initialValues.surename,
    //    login : ownProps.initialValues.login,
    //    role : ownProps.initialValues.role
    //}
  }
}

const mapDispatchToProps = dispatch =>({
  getOne : (uId, id) => dispatch(getUser(uId,id)),
  createOne : (uId, formData) => dispatch(insertUser(uId, formData)),
  updateOne : (uId, formData)=> dispatch(updatedUser(uId, formData)),
  loadSettings : () => dispatch(getSettings())
})

export default connect(mapStateToProps, mapDispatchToProps)(reduxForm({
  form:'UserForm',
  validate,
  enableReinitialize : true,
  destroyOnUnmount: true
})(UserEdit))

